import React, { Component } from 'react';
import styled, { css, keyframes } from 'react-emotion';
import VisionImage from './VisionImage';
import ImageIndicator from './ImageIndicator';


const IMAGES = [
  'https://images.collection.cooperhewitt.org/18907_c8619685c9af9b3f_o.jpg',
  'https://upload.wikimedia.org/wikipedia/commons/c/c4/Cerambycidae_-_Dorcacerus_barbatus.jpg',
  'https://images.collection.cooperhewitt.org/3523_4ea78776db5605bc_b.jpg',
  'https://media.vam.ac.uk/media/thira/collection_images/2006AA/2006AA4341.jpg',
  'https://images.collection.cooperhewitt.org/181485_b748a40d55f23d4c_b.jpg',
  'https://images.collection.cooperhewitt.org/61628_9f59621770e3c33e_x.jpg',
  'https://images.collection.cooperhewitt.org/61337_9aba4b71d1a69805_b.jpg',
  'https://images.collection.cooperhewitt.org/90459_fffbbc85300d1530_b.jpg',
  'https://images.collection.cooperhewitt.org/53995_c7a83d90e1a7e16a_x.jpg',
  'https://images.collection.cooperhewitt.org/65610_d26d06dd31c8fd8c_b.jpg',
  'https://images.collection.cooperhewitt.org/82336_33dc4f377f098de8_b.jpg',
  'https://images.collection.cooperhewitt.org/90438_d3492b28e79eb7b7_x.jpg',
  'https://lh3.googleusercontent.com/H2YHL1UHXEDmA5O9GjUABMvvgXEfnWSuEZw1Ro84aRaY1B5fegxcrepZfQw2WAIPIvNoHtqpjY4h6EAyZp6e0VRqyAc=s0',
  'https://images.collection.cooperhewitt.org/182324_a212b44c7eb32c5d_b.jpg',
  'https://media.vam.ac.uk/media/thira/collection_images/2006BH/2006BH7964.jpg',
  'https://lh3.googleusercontent.com/ohfKfi9aPgVDmhfIRlTPTyiYFTZ8jm6N9SGFMHOvBTGDHRRXDgZA6F2FnioBT0W3yD4x-yA8cFTh5dMdOWc96T59n22X=s0',
  'https://images.collection.cooperhewitt.org/51068_6e56e44554204285_b.jpg',
  'https://images.collection.cooperhewitt.org/262750_bb51e977a2385cce_b.jpg',
  'https://images.collection.cooperhewitt.org/180509_5dc12bcaec4e2137_b.jpg',
  'https://upload.wikimedia.org/wikipedia/commons/0/0b/AgrihanNASA.jpg',
  'https://nrs.harvard.edu/urn-3:HUAM:LEG001920_dynmc',
  'https://images.collection.cooperhewitt.org/45311_88fafb1773232e08_b.jpg',
];

class App extends Component {
  state = {
    viewed: [],
    inView: [],
  }

  shouldComponentUpdate(nextProps, nextState) {
    return nextState.inView.length !== this.state.inView.length;
  }

  onIntersect = (inView, viewed) => {
    this.setState({ inView });
  }

  onLoad = (viewed) => {
    this.setState({
      loadState: 'loaded',
      viewed: window.VISION3K.viewed,
    }, this.forceUpdate);
  }

  _sortJoin = arr => arr.sort((a, b) => a - b).join(', ');

  render() {
    const { inView , viewed } = this.state;

    return (
      <main>
        <ImageIndicator length={IMAGES.length} inView={inView} viewed={viewed} />

        <Flex>
          { IMAGES.map((src, index) => (
            <Wrap
              key={index}
              id={`image-${index}`}
              tile={index > 2 && index < 7}
              half={index > 11 && index < 14}>
              <VisionImage
                src={src}
                classNames={{
                  containerBase: styles.container,
                  imageBase: styles.image,
                  imageLoaded: styles.imageLoaded,
                }}
                onIntersect={this.onIntersect}
                onLoad={this.onLoad} />
            </Wrap>
          ))}
        </Flex>
      </main>
    );
  }
}

const fadeIn = keyframes`
  0% { opacity: 0; }
  100% { opacity: 1; }
`;

const Flex = styled('div')`
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 30px;
`;


const Wrap = styled('div')`
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;

  ${props => props.tile && `
    width: 50%;
    height: 50vh;
  `}

  ${props => props.half && `
    width: calc(50% - 30px);
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;

    figure {
      width: 100%;
      height: auto;

      img {
        width: 100%;
        height: auto;
      }
    }
  `}
`;

const styles = {
  container: css`
    box-sizing: border-box;
    position: relative;
    margin: 0;
    overflow: hidden;
    background-color: #121212;

    @media (orientation: landscape) {
      min-width: 40%;
      height: calc(100% - 60px);
    }

    @media (orientation: portrait) {
      width: calc(100% - 60px);
      min-height: 40%;
    }
  `,
  image: css`
    display: block;
    opacity: 0;

    @media (orientation: landscape) {
      height: 100%;
    }

    @media (orientation: portrait) {
      width: 100%;
    }
  `,
  imageLoaded: css`
    animation: ${fadeIn} 0.5s 0.1s forwards;
  `,
  viewed: css`
    color: green;
  `,
}

export default App;



